<template>
  <div class="order-detail">
    <div class="order-info ">
      <p class=" number-state">
        <span class="number">订单编号：{{orderDetail.sn}}</span>
        <span class="state">{{orderStatus(orderDetail.status)}}</span>
      </p>
      <div class="title-logo ">
        <p class="company-title">{{orderDetail.name}}</p>
        <p class="logo"><img :src="baseSrc+bbCompanyInfo.logo" alt="公司logo"></p>
      </div>

      <div class="info-wrapper ">
        <p class="info" v-if="bbxrInfo.policy_num">保单号：{{bbxrInfo.policy_num}}</p>
        <p class="info">被保险人：{{bbxrInfo.name}}</p>
        <p class="info">保障期间：{{orderDetail.policy_effective_date}}至{{orderDetail.policy_expiration_date}}</p>
        <p class="info">保险公司：{{bbCompanyInfo.name}}</p>
        <p class="info" v-if="orderDetail.policy_num">保单号：{{orderDetail.policy_num}}</p>
      </div>
      <p class="price">{{orderDetail.price}}<span>元</span></p>
    </div>
    <div class="layer bzfa-wrapper ">
      <div class="layer bzfa-layer">
        <div class="title-name " @click="bzfaShow=!bzfaShow">
          <span class="title">保障方案</span>
          <p class="name"  v-if="bzfaInfo.plan_name">
            <span>{{bzfaInfo.plan_name}}</span>
            <i :style="bzfaShow ? upImg : downImg"></i>
          </p>
        </div>
        <div class="bzfa-wrapper" v-show="bzfaShow" v-for="(item,index) in planInfo" :key="index">
            <div class="plan-info-item">
              <div class="oneDiv"  @click="detailShow(index)">
               <span class="bzxm">{{item.name}} </span>
               <div class="bzsm" >
                <span >{{item.value}}</span>
                <i :style="moreArr[index] ? upImg : downImg"></i>
              </div>
              </div>
              <div class="twoDiv" v-show="moreArr[index]">
                <div class="bzed">
                  <span>{{item.detail}}</span>
                </div>
              </div>

          </div>
        </div>

      </div>
    </div>
    <div class="other-info ">
      <div class="layer ">
        <div class="title-name" @click="ttrShow=!ttrShow">
          <span class="title">投保人信息</span>
          <p class="name" >
            <span class="name">{{tbrInfo.name}}</span>
            <i :style="ttrShow ? upImg : downImg"></i>
          </p>
        </div>
        <div class="info-wrapper" v-show="ttrShow">
          <p class="info info-ts"><span>姓名</span><span>{{tbrInfo.name}}</span></p>
          <p class="info info-ts"><span>性别</span><span>{{getSexByName(tbrInfo.gender)}}</span></p>
          <p class="info info-ts"><span>身份证号</span><span>{{tbrInfo.id_num}}</span></p>
          <p class="info info-ts"><span>出生日期</span><span>{{dateFormat(tbrInfo.birth)}}</span></p>
          <p class="info info-ts"><span>联系电话</span><span>{{tbrInfo.mobile}}</span></p>
        </div>
      </div>
      <div class="layer layer-ts ">
        <div class="title-name" @click="bttrShow=!bttrShow">
          <span class="title">被保险人信息</span>
          <p class="name" >
            <span class="name">{{bbxrInfo.name}}</span>
            <i :style="bttrShow ? upImg : downImg"></i>
          </p>
        </div>
        <div class="info-wrapper" v-show="bttrShow">
          <p class="info info-ts"><span>姓名</span>{{bbxrInfo.name}}</p>
          <p class="info info-ts"><span>性别</span>{{getSexByName(bbxrInfo.gender)}}</p>
          <p class="info info-ts"><span>身份证号</span>{{bbxrInfo.id_num}}</p>
          <p class="info info-ts"><span>出生日期</span>{{dateFormat(bbxrInfo.birth)}}</p>
          <p class="info info-ts"><span>是投保人的</span>{{getRelation(bbxrInfo.type)}}</p>
        </div>
      </div>
      <div class="layer ">
        <p class="title-name ">
          <span class="title">受益人</span>
          <span class="name name-syr">{{orderDetail.beneficiary}}</span>
        </p>
      </div>
      <div class="layer layer-order">
        <p class="title ">
          订单信息
        </p>
        <div class="info-wrapper">
          <div class="wrapper"
               v-if="orderDetail.status===1||orderDetail.status===2||orderDetail.status===3||orderDetail.status===4||orderDetail.status===5||orderDetail.status===6">
            <p class="info "><span>支付方式</span>{{payInfo.paymentPluginName}}</p>
            <p class="info"><span>支付金额</span>¥{{payInfo.amount}}</p>
            <p class="info "><span>支付流水号</span>{{payInfo.transactionId}}</p>
            <p class="info"><span>付款时间</span>{{payInfo.create_time}}</p>
          </div>
          <p class="info"><span>订单编号</span>{{orderDetail.sn}}</p>
          <p class="info"><span>创建时间</span>{{orderDetail.create_time}}</p>
        </div>
      </div>
      <div class="btns">
        <div class="btn baodan" v-if="orderDetail.policy_url!==null">电子保单</div>
        <div class="btn againPay" v-if="[5,6,7].filter(i=>i===orderDetail.status).length" @click='handleRePay(orderDetail)'>再次购买</div>
        <div class="btn toSettle" v-if="[4].filter(i=>i===orderDetail.status).length || ([5].filter(i=>i===orderDetail.status).length && dateDiff(orderDetail.policy_expiration_date))">申请理赔</div>
        <div class="btn toPay" v-if="orderDetail.status===0" @click='handleToPay(orderDetail)'>立即支付</div>
        <div class="btn rePay" v-if="[8].filter(i=>i===orderDetail.status).length" @click='handleToPay(orderDetail)'>重新支付</div>
        <div class="btn toDetail" v-if="[0,1,2,3,4,5,6,7,8].filter(i=>i===orderDetail.status).length" @click="goDetail(orderDetail)">查看详情</div>
      </div>
    </div>
  </div>
</template>

<script>

import { orderStatus, getSexByName, dateFormat, getRelation } from '@/assets/js/mixins/base.js'

export default {
  mixins: [orderStatus, getSexByName, dateFormat, getRelation],
  data () {
    return {
      // moreArr: [],
      bzfaShow: true,
      bttrShow: false,
      ttrShow: false,
      orderDetail: {},
      tbrInfo: {},
      bbxrInfo: {},
      bbCompanyInfo: {},
      bzfaInfo: {},
      payInfo: {},
      planInfo: [],
      baseSrc: '',
      moreArr: [],
      upImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_up.png') + ')'
      },
      downImg: {
        'background-image':
          'url(' + require('@/assets/images/icon_down.png') + ')'
      }
    }
  },
  mounted () {
    this.getOrderDetail()
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    //
  },
  methods: {
    handleRePay (item) { // 再次购买
      this.$router.push({
        query: {
          productId: item.product_id,
          orderSn: item.sn
        },
        path: `/product/detail/${item.product_id}`
      })
    },
    handleToPay (item) { // 重新支付 //立即支付
      this.$router.push({
        query: {
          sn: item.sn,
          productId: item.product_id
        },
        path: `/product/orderConfirm`
      })
    },
    goDetail (item) {
      this.$router.push({
        path: `/product/detail/${item.id}`,
        query: {
          orderId: item.id,
          productId: item.product_id
        }
      })
    },
    detailShow (index) {
      this.moreArr.forEach((item, i) => { // 循环已经把所有的状态值清空了
        if (index !== i) {
          this.moreArr.splice(i, 1, false)
        }
      })
      this.moreArr.splice(index, 1, !this.moreArr[index])
    },
    getOrderDetail () {
      let postData = {
        orderId: this.$route.query.orderId
      }
      this.$api.personCenter.orderDetail(postData).then(res => {
        if (res.code === 1) {
          this.orderDetail = res.data
          this.tbrInfo = res.data.member_info
          this.bbxrInfo = res.data.relation_info
          this.bbCompanyInfo = res.data.brand_info
          this.bzfaInfo = res.data.plan_info
          this.planInfo = res.data.plan_info.plan_details
          this.payInfo = res.data.payment_info
          // this.moreArr.push(false)
          this.productInfo = res.data.product_info.parameter_values[0].entries
          for (let i = 0; i < this.planInfo.length; i++) {
            this.moreArr.push(false)
            if (this.planInfo[i].name === res.data.plan_info.plan_details[i].name) {
              this.$set(this.planInfo[i], 'detail', res.data.product_info.parameter_values[0].entries[i].value)
            }
          }
          console.log(123, res.data)
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .order-detail {
    position: fixed;
    z-index: 100;
    top: 0.84rem;
    left: 0;
    bottom: pxToRem(100px);
    right: 0;
    background: #f5f5f5;
    overflow-y: scroll;
    .order-info {
      background: #fff;
      padding-left: pxToRem(30px);
      margin-bottom: pxToRem(20px);
      .number-state {
        font-size: pxToRem(30px);
        color: #A5A8A8;
        height: pxToRem(82px);
        line-height: pxToRem(82px);
        border-bottom: $border-bottom-light-color;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .number {
          flex: 1;
        }
        .state {
          width: pxToRem(140px);
          padding-right: pxToRem(30px);
          text-align: center;
        }
      }
      .title-logo {
        margin-top: pxToRem(30px);
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: pxToRem(30px);
        .company-title {
          font-size: pxToRem(36px);
          color: $color-text;
          flex: 1;
          height: pxToRem(60px);
        }
        .logo {
          width: pxToRem(110px);
          height: pxToRem(60px);
          img {
            display: block;
            width: pxToRem(110px);
            height: pxToRem(60px);
          }
        }
      }
      .price {
        font-size: pxToRem(40px);
        color: $color-active-text;
        height: pxToRem(90px);
        line-height: pxToRem(90px);
        span {
          font-size: pxToRem(24px);
          color: #C5C5C5;
        }
      }
      .info-wrapper {
        position: relative;
        .info {
          font-size: pxToRem(26px);
          line-height: pxToRem(42px);
          width: 100%;
        }
        .info-ts {
          font-size: pxToRem(24px);
          line-height: pxToRem(38px);
        }

      }
    }
    .bzfa-layer {
      .bzfa-wrapper {
        padding-left: pxToRem(30px);
        background: #F1F4F4;
        .plan-info-item {
          .oneDiv{
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #D5D8D8;
            padding-right: pxToRem(20px);
            height: pxToRem(86px);
            line-height: pxToRem(86px);
          }
          .twoDiv{
            font-size: pxToRem(24px);
            color: #A5A8A8;
            line-height: pxToRem(38px);
            border-bottom: 1px solid #D5D8D8;
            padding:pxToRem(12px) pxToRem(30px) pxToRem(12px) 0;
          }
          .bzsm {
            display: flex;
            justify-content: space-between;
            align-items: center;
            i {
              display: inline-block;
              width: pxToRem(44px);
              height: pxToRem(44px);
              background-repeat: no-repeat;
              background-position: right center;
              background-size: cover;
            }
          }

        }
        &:last-child{
          .plan-info-item{
            .twoDiv{
              border-bottom:none;
            }
          }

        }
      }

    }
    .layer {
      background: #fff;

      &:last-child {
        margin-right: 0;
      }
      .title-name {
        padding-left: pxToRem(30px);
        display: flex;
        justify-content: space-between;
        border-bottom: $border-bottom-light-color;
        padding-right: pxToRem(20px);
        height: pxToRem(91px);
        line-height: pxToRem(91px);
        .title {
          font-size: pxToRem(28px);
          color: $color-text;
          box-sizing: border-box;

        }
        .name {
          font-size: pxToRem(28px);
          color: $color-text;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          i {
            display: inline-block;
            width: pxToRem(44px);
            height: pxToRem(44px);
            background-repeat: no-repeat;
            background-position: right center;
            background-size: cover;
          }
        }
        .name-syr {
          padding-right: pxToRem(30px);
        }
      }
      .info-wrapper {
        position: relative;
        border-bottom: $border-bottom-light-color;
        padding: pxToRem(16px) pxToRem(42px) pxToRem(17px) pxToRem(36px);
        background: #F1F4F4;
        .info {
          font-size: pxToRem(26px);
          color: #A5A8A8;
          line-height: pxToRem(42px);
          display: flex;
          justify-content: space-between;
          width: 100%;
          .tbr {
            margin-right: 110px;
          }
        }
        .info-ts {
          font-size: pxToRem(24px);
          line-height: pxToRem(38px);
        }
        .info-lsh {
          line-height: 24px;
          margin-top: -6px;
          margin-bottom: 6px;
        }

      }

    }
    .layer-order {
      background: #FFFFFF;
      margin-top: pxToRem(20px);
      .title {
        font-size: pxToRem(28px);
        color: $color-text;
        position: relative;
        padding: pxToRem(30px) 0 pxToRem(14px) pxToRem(74px);
        &:before {
          content: '';
          position: absolute;
          top: pxToRem(42px);
          left: pxToRem(30px);
          width: pxToRem(28px);
          height: pxToRem(4px);
          background: $color-active-text;
        }
      }
      .info-wrapper {
        padding-left: pxToRem(30px);
        background: #fff;
      }
    }
    .btns {
      position: fixed;
      bottom:0;
      width:100%;
      /*margin-top: pxToRem(20px);*/
      background: #fff;
      height: pxToRem(100px);
      line-height: pxToRem(100px);
      display: flex;
      justify-content: flex-end;
      align-items: center;
      .btn {
        width: pxToRem(150px);
        height: pxToRem(60px);
        line-height: pxToRem(60px);
        margin-right: pxToRem(20px);
        font-size: pxToRem(24px);
        border-radius: pxToRem(34px);
        text-align: center;
        color: #B6BBBB;
        &.toDetail {
          color: #FFFFFF;
          background: #1FD8D2;
          border: 1px solid $color-active-text;
        }
        &.toPay, &.againPay, &.rePay {
          color: #FF4B4B;
          background: #FFFFFF;
          border: 1px solid #FF4B4B;
        }
        &.baodan {
          color: #A5A8A8;
          border: 1px solid #D5D8D8;
          background: #FFFFFF;
        }
        &.toSettle {
          color: #1FD8D2;
          border: 1px solid #1FD8D2;
          background: #FFFFFF;
        }
      }
    }
  }
</style>
